.rmc-picker-scroller-wrapper,
.rmc-picker-scroller {
  width: 100%;
}
.rmc-picker-scroller-wrapper {
  display: block;
  position: relative;
  height: 238px;
  overflow: hidden;
}
.rmc-picker-scroller {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
      text-size-adjust: none;
  /* Prevent elements to be highlighted on tap */
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  /* Put the scroller into the HW Compositing layer right from the start */
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.rmc-picker-scroller-item {
  text-align: center;
  font-size: 16px;
  height: 34px;
  line-height: 34px;
  color: #000;
}
.rmc-picker-scroller-mask {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
